.heading-block {
  min-width: 280px;
  margin: 32px auto 16px auto;
  text-align: center;
}

.heading-block__head {
  margin: 0;
  padding: 0;
  padding-bottom: 35px;
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}

.heading-block__heading-text {
  margin: 0;
  padding: 0;
  padding-bottom: 32px;
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  color: $dark-brown;
  text-transform: uppercase;
}

