.navigation-menu {
  width: 320px;
}

.navigation-menu__list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navigation-menu__item {
  box-sizing: border-box;
  width: 320px;
  min-height: 55px;
  background-color: $blue;
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: $white;
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.navigation-menu__item--logo {
  display: none;
}

.navigation-menu__item:first-child {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}

.navigation-menu__item:last-child {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}

.navigation-menu__item:last-child::after {
  content: " ";
  width: 320px;
  height: 7px;
  z-index: 5;
  position: relative;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}

.navigation-menu__link {
  color: $white;
  display: block;
  width: 320px;
  padding-bottom: 12px;
  padding-top: 17px;
  text-decoration: none;
}

.navigation-menu__link:hover {
  color: $white;
  background-color: #669ec0;
}

.navigation-menu__link:active {
  color: $white;
  opacity: 0.3;
  background-color: #5496bd;
}


